>1.正常渲染info数据
>2.进行滚动
>3.有一部分时横向滚动的 展示图片的 需要根据图片指定宽度 然后在进行new scroll 配置横向滚动即可

<template>
  <div class="shop-info">
    <div class="info-content">
      <section class="section">
        <h3 class="section-title">配送信息</h3>
        <div class="delivery">
          <div>
            <span class="delivery-icon">{{ info.description }}</span>
            <span
              >由商家配送提供配送，约{{ info.deliveryTime }}分钟送达，距离{{
                info.distance
              }}</span
            >
          </div>
          <div class="delivery-money">配送费￥{{ info.deliveryPrice }}</div>
        </div>
      </section>

      <Split />

      <section class="section">
        <h3 class="section-title">活动与服务</h3>
        <div class="activity">
          <div
            class="activity-item"
            v-for="(support, index) in info.supports"
            :key="index"
            :class="supportClasses[support.type]"
          >
            <span class="content-tag">
              <span class="mini-tag">{{ support.name }}</span>
            </span>
            <span class="activity-content">{{ support.content }}</span>
          </div>
        </div>
      </section>

      <Split />

      <section class="section">
        <h3 class="section-title">商家实景</h3>
        <div class="pic-wrapper">
          <ul class="pic-list" ref="picsUl">
            <li class="pic-item" v-for="pic in info.pics" :key="pic">
              <img width="120" height="90" v-lazy="pic" />
            </li>
          </ul>
        </div>
      </section>

      <Split />

      <section class="section">
        <h3 class="section-title">商家信息</h3>
        <ul class="detail">
          <li>
            <span class="bold">品类</span>
            <span>{{ info.category }}</span>
          </li>
          <li>
            <span class="bold">商家电话</span>
            <span>{{ info.phone }}</span>
          </li>
          <li>
            <span class="bold">地址</span>
            <span>{{ info.address }}</span>
          </li>
          <li>
            <span class="bold">营业时间</span>
            <span>{{ info.workTime }}</span>
          </li>
        </ul>
      </section>
    </div>
  </div>
</template>

<script>
import BScroll from "@better-scroll/core";
import { mapState } from "vuex";
export default {
  name: "Info",
  data() {
    return {
      supportClasses: ["activity-green", "activity-red", "activity-orange"]
    };
  },
  computed: {
    ...mapState({
      info: state => state.shop.info
    })
  },

  mounted() {
    if (this.info.name) {
      // 从其它路由切换过来, 数据已经有了
      this._initScroll();
    }
  },

  watch: {
    info() {
      // 在当前路由刷新: 初始显示没有数据, 后面才有了数据
      this.$nextTick(() => {
        this._initScroll();
      });
    }
  },

  methods: {
    _initScroll() {
      // 整体垂直滑动
      new BScroll(".shop-info", {
        click: true
      });

      // 给ul指定样式宽度
      const ul = this.$refs.picsUl;
      const liWidth = 120; //一个图片的宽度
      const space = 6; //图片间隙
      const liCount = this.info.pics.length; //图片个数
      const width = (liWidth + space) * liCount - space;
      // 120+685 = 630-6 = 624

      //讲解↓
      // 6为间隙 5张图片中间有4调缝
      // 120*5=600 + 684 = 624
      console.log(width);
      ul.style.width = width + "px";
      // 水平滑动
      new BScroll(".pic-wrapper", {
        click: true,
        scrollX: true // 水平滑动
      });
    }
  }
};
</script>


<style lang="stylus" rel="stylesheet/stylus">
@import '../../../assets/stylus/mixins';

.shop-info {
  position: absolute;
  top: 250px;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #fff;
  overflow: hidden;

  .section {
    padding: 16px 14px 14px;
    font-size: 16px;
    background-color: #fff;
    color: #666;
    border-bottom: 1px solid #eee;
    position: relative;

    .section-title {
      color: #000;
      font-weight: 700;
      line-height: 16px;

      > .iconfont {
        float: right;
        color: #ccc;
      }
    }

    .delivery {
      margin-top: 16px;
      font-size: 13px;
      line-height: 18px;

      .delivery-icon {
        font-size: 11px;
        margin-right: 10px;
        display: inline-block;
        text-align: center;
        color: #fff;
        background-color: #0097ff;
        padding: 1px 0;
        border-radius: 4px;
      }

      .delivery-money {
        margin-top: 5px;
      }
    }

    .activity {
      margin-top: 16px;

      .activity-item {
        margin-bottom: 12px;
        display: flex;
        font-size: 13px;
        align-items: center;

        &.activity-green {
          .content-tag {
            background-color: #01affd;
          }
        }

        &.activity-red {
          .content-tag {
            background-color: rgb(240, 115, 115);
          }
        }

        &.activity-orange {
          .content-tag {
            background-color: rgb(241, 136, 79);
          }
        }

        .content-tag {
          display: inline-block;
          border-radius: 2px;
          width: 36px;
          height: 18px;
          margin-right: 10px;
          color: #fff;
          font-style: normal;
          position: relative;

          .mini-tag {
            position: absolute;
            left: 0;
            top: 0;
            right: -100%;
            bottom: -100%;
            font-size: 24px;
            transform: scale(0.5);
            transform-origin: 0 0;
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }
      }
    }

    .pic-wrapper {
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
      margin-top: 16px;

      .pic-list {
        font-size: 0;

        .pic-item {
          display: inline-block;
          margin-right: 6px;
          width: 120px;
          height: 90px;

          &:last-child {
            margin: 0;
          }
        }
      }
    }

    .detail {
      margin-bottom: -16px;

      > li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-right: -10px;
        padding: 16px 12px 16px 0;
        line-height: 16px;
        bottom-border-1px(#ddd);
        font-size: 13px;

        > .bold {
          font-weight: 700;
          color: #333;
        }

        &:last-child {
          border-none();
        }
      }
    }
  }
}
</style>
